<template>
  <div class="shop">
    <el-row>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs>
          <el-tab-pane label="Order">
            <el-table :data="tableData" border style="width:100%">
              <el-table-column prop="goodsName" label="Goodsname"></el-table-column>
              <el-table-column prop="count" label="Count"></el-table-column>
              <el-table-column prop="price" label="Price"></el-table-column>
              <el-table-column prop="do" label="Do" fixed="right">
                <template slot-scope="scope">
                  <el-button type="text" size="small" @click="delSingleGoods(scope.row)">delete</el-button>
                  <el-button type="text" size="small" @click="addOrderList(scope.row)">add</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div
              class="totaldiv"
            >Number:{{totalCount}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Total price：{{totalMoney}}</div>
            <div class="div-button">
              <el-button type="danger" @click="delAllGoods">CANCAL</el-button>
              <el-button type="success" @click="checkout">BUY</el-button>
            </div>
          </el-tab-pane>

          <el-tab-pane label="Buy">Buy</el-tab-pane>
          <el-tab-pane label="Takeaway">Takeaway</el-tab-pane>
        </el-tabs>
      </el-col>

      <el-col :span="17">
        <div class="often-goods">
          <div class="title">favorite goods</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">{{goods.price}}EUR</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="buger">
              <div>
                <ul class="cookList">
                  <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%" />
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">{{goods.price}}EUR</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="snack">
              snack
              <ul class="cookList">
                <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.goodsName}}</span>
                  <span class="foodPrice">{{goods.price}}EUR</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="drink">
              drink
              <ul class="cookList">
                <li v-for="goods in type2Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.goodsName}}</span>
                  <span class="foodPrice">{{goods.price}}EUR</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="menu">
              menu
              <ul class="cookList">
                <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="100%" />
                  </span>
                  <span class="foodName">{{goods.goodsName}}</span>
                  <span class="foodPrice">{{goods.price}}EUR</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
//addlist @click="addOrderList(scope.row)"
import axios from "axios";
export default {
  name: "shop",
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalMoney: 0,
      totlaCount: 0
    };
  },
  created: function() {
    axios
      .get(
        "https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/oftenGoods"
      )
      .then(reponse => {
        //console.log(reponse);
        this.oftenGoods = reponse.data.oftenGoods;
      })
      .catch(error => {
        alert("Internet Error");
      });
    axios
      .get(
        "https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/typeGoods"
      )
      .then(reponse => {
        //console.log(reponse);
        this.type0Goods = reponse.data.data[0];
        this.type1Goods = reponse.data.data[1];
        this.type2Goods = reponse.data.data[2];
        this.type3Goods = reponse.data.data[3];
      })
      .catch(error => {
        alert("Internet Error");
      });
  },
  
  mounted: function() {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  methods: {
    addOrderList(goods) {
        this.totalCount=0;
       this.totalMoney=0;
      //exist in list
      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      //yes add num no add goods
      if (isHave) {
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
        this.tableData.forEach((element) => {
        this.totalCount+=element.count;
        this.totalMoney=this.totalMoney+(element.price*element.count);   
       });
        };
        
    },
       //del goods
    delSingleGoods(goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
    },
    delAllGoods() {
     this.tableData = [];
     this.totalCount = 0;
     this.totalMoney = 0;
},
    checkout(){
      if(this.totalCount!=0){
        this.tableData=[];
        this.totlaCount=0;
        this.totalMoney=0;
        this.$message({
            message:"Thank You!",
            type:'success'
        });
      }else{
        this.$message.error('Please add items before checkout!')
      }
    }
  
    },
 
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order {
  background-color: white;
  border-right: 1px solid #c0ccda;
}
.div-button {
  margin-top: 10px;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #58b7ff;
}
.goods-type {
  clear: both;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.totaldiv {
  background-color: white;
  padding: 10px;
  border-bottom: 1px solid;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>


